<template>
  <div id='recommend' ref="recommend">
    <scroll class="recommend-content" :data="discList" ref='scroll'>
      <div>
        <div class="slider-warp" v-if='recommend.length'>
          <div class="slider-content">
            <slider ref="slider">
              <div v-for='item in recommend'>
                <a :href='item.linkUrl'>
                  <img @load="_loadImage" :src="item.picUrl"/>
                </a>
              </div>
            </slider>
          </div>
        </div>
        <div class="recommend-list">
          <h1 class="list-title">热门歌曲推荐</h1>
          <song-list :songs="discList"  @select="selectItem" :showImg="showImg"></song-list>
        </div>
      </div>
      <div class="loading-wrapper" v-show="!discList.length">
        <loading></loading>
      </div>
    </scroll>
  </div>
</template>

<script>
  import {getRecommend} from '@/api/recommend'
  import {getDisc} from '@/common/js/author'
  import {ERR_OK} from '@/api/config'
  import Slider from '@/base/slider/slider'
  import Scroll from '@/base/scroll/scroll'
  import Loading from '@/base/loading/loading'
  import SongList from '@/base/song-list/song-list'
  import {playlistMixin} from '@/common/js/mixin'
  import {mapActions} from 'vuex'
  import {search} from '@/api/search'
  import {createSong} from '@/common/js/song'
  export default {
    mixins: [playlistMixin],
    data(){
      return {
        recommend: [],
        discList: [],
        showImg:true
      }
    },
    created(){
      this._getRecommend()
      this._getDiscList()
    },
    activated() {
      setTimeout(() => {
        this.$refs.slider && this.$refs.slider.refresh()
        this.$refs.scroll && this.$refs.scroll.refresh()
      }, 20)
    },
    methods:{
      _getRecommend(){
        getRecommend().then((res)=>{
          if(ERR_OK===res.code){
            this.recommend=res.data.slider
          }
        })
      },
      _getDiscList(){
        getDisc().then((res)=>{  
         this._getResult(res)
        })
      },
      _getResult(res){
        let ret = []
        res.forEach((item)=>{
          search(item,1,false,1).then((res)=>{
            ret = ret.concat(this._normalize(res.data.song.list))
            this.discList=ret
          })
        })
       
      },
      _normalize(list){
        let ret = []
        list.forEach((musicData) => {
          ret.push(createSong(musicData))
        })
        return ret
      },
      _loadImage() {
        if (!this.checkloaded) {
          this.checkloaded = true
          setTimeout(() => {
            this.$refs.scroll.refresh()
          }, 20)
        }
      },
      selectItem(song,index){
         this.selectPlay({
          list:this.discList,
          index,index
        })
      },
      handlePlaylist(playlist){
        const bottom = playlist.length > 0 ? '60px' : ''
        this.$refs.recommend.style.bottom = bottom
        this.$refs.scroll.refresh()
      },
      ...mapActions([
        'selectPlay'
      ])
    },
    components:{
      Slider,
      Scroll,
      Loading,
      SongList
    }
  }
</script>

<style scoped rel='stylesheet/stylus' lang='stylus'>
  @import "~@/common/stylus/var"
  #recommend
    position fixed
    top 88px
    bottom 0
    width 100%
    background-color $color-background
    .recommend-content
      height 100%
      overflow hidden
      .slider-warp
        position relative
        width 100%
        height 0
        padding-top 40%
        overflow hidden
        .slider-content
          position: absolute
          top: 0
          left: 0
          width: 100%
          height: 100%
      .recommend-list
        padding 10px 20px
        .list-title
          height: 60px
          line-height: 60px
          text-align: center
          font-size: $font-size-medium
          color: $color-theme
      .loading-wrapper
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>



