<template>
  <div class="slider" ref="slider">
    <div class="slider-group" ref="sliderGroup">
      <slot></slot>
    </div>
    <div class="dots">
      <span class="dot" v-for="(item,index) in dots" :class="{active:currentPageIndex===index}"></span>
    </div>
  </div>
</template>

<script>
  import {addClass} from '@/common/js/dom'
  import BScroll from 'better-scroll'
  export default {
    data(){
      return{
        dots: [],
        currentPageIndex: 0
      }
    },
    props:{
      loop:{
        type: Boolean,
        default: true
      },
      autoPlay:{
        type: Boolean,
        default: true
      },
      interval:{
        type: Number,
        default: 4000
      }
    },
    mounted(){
      setTimeout(()=>{
        this._initWidth()
        this._initDots()
        this._initSlider()
        if(this.autoPlay){
          this._play()
        }
      },20)

      window.addEventListener('resize', () => {
        if (!this.slider) {
          return
        }
        this._initWidth(true)
        this.slider.refresh()
      })
    },
    activated(){
      this._play()
    },
    deactivated() {
      clearTimeout(this.timer)
    },
    beforeDestroy() {
      this.slider.disable()
      clearTimeout(this.timer)
    },
    methods:{
      _initWidth(isResize){
        this.children = this.$refs.sliderGroup.children

        let width = 0
        let sliderWidth=this.$refs.slider.clientWidth
        for(let i = 0 ;i < this.children.length; i++){
          let child=this.children[i]
          addClass(child,'slider-item')
          child.style.width=sliderWidth +'px'
          width +=  sliderWidth
        }
        if(this.loop && !isResize){
           width += 2*sliderWidth
        }
        this.$refs.sliderGroup.style.width = width +'px'
      },
      _initSlider(){
        this.slider=new BScroll(this.$refs.slider,{
          scrollX: true,
          scrollY: false,
          momentum: false,
          click: false,
          snap: {
            loop: this.loop,
            threshold: 0.3,
            speed: 400
          }
        })

        this.slider.on('scrollEnd',this._onScrollEnd)

        this.slider.on('beforeScrollStart', () => {
          if (this.autoPlay) {
            clearTimeout(this.timer)
          }
        })
      },
      _initDots(){
        this.dots=new Array(this.children.length)
      },
      _onScrollEnd(){
        let pageIndex = this.slider.getCurrentPage().pageX
        if (this.loop) {
          pageIndex -= 1
        }
        this.currentPageIndex = pageIndex
        if (this.autoPlay) {
          this._play()
        }
      },
      _play(){
        let pageIndex = this.slider.getCurrentPage().pageX + 1
        clearTimeout(this.timer)
        this.timer = setTimeout(() => {
          this.slider.goToPage(pageIndex, 0, 400)
        }, this.interval)
      },
      refresh(){
        this._initWidth(true)
        this.slider && this.slider.refresh()
      }
    }
  }

</script>

<style scoped lang='stylus' ref='stylesheet/stylus'>
  @import "~@/common/stylus/var"
  .slider
    min-height: 1px
    .slider-group
      position relative
      overflow hidden
      .slider-item
        float: left
        box-sizing: border-box
        overflow: hidden
        text-align: center
        a
          display: block
          width: 100%
          overflow: hidden
          text-decoration: none
        img
          display: block
          width: 100%
    .dots
      position: absolute
      right: 0
      left: 0
      bottom: 12px
      transform: translateZ(1px)
      text-align: center
      font-size: 0
      .dot
        display: inline-block
        margin: 0 4px
        width: 8px
        height: 8px
        border-radius: 50%
        background: $color-text-l
        &.active
          width: 20px
          border-radius: 5px
          background: $color-white
</style>



